body {
  margin: 0;
  padding: 0;
}

.section {
  height: 108vh;
}

.hero-page {
  background-color: #353d40;
}

/* Navbar ============================= */
.nav-container {
  padding-top: 2%;
}
/* ================= */
.navbar-default {
  background-color: #353d40;
  /* background-color: #771db3; */
  width: 90vw;
}

.navbar-default .navbar-brand {
  width: 11%;
  height: 80px;
  position: relative;
  margin-left: 200px;
}

.logo {
  position: relative;
  bottom: 30px;
  width: 98%;
}

.navbar-default .navbar-nav > li > a {
  color: #d9d9d9;
}

.menu-icon {
  color: #f2b138;
}

.myNav {
  margin-right: 9%;
}

.nav-item {
  color: white;
  margin-right: 28px;
  font-family: "Raleway", sans-serif;
  font-size: 1rem;
}
/* ==================================================LANDING */
.card {
  background-color: #003f63;
  box-shadow: 0px 9px 6px 5px rgba(0, 0, 0, 0.25);
  border-radius: 18px;
  padding-bottom: 3%;
  position: relative;
  top: 144px;
  right: 1.5%;
  width: 75%;
}

.content {
  margin-top: 3.75rem;
  margin-left: 40px;
  /* background-color: rgb(59, 63, 63); */
}

.greet {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 1.37rem;
  letter-spacing: 0.1rem;
  color: #d9d9d9;
}

.heading {
  font-size: clamp(40px, 3vw, 80px);
  font-family: "Montserrat", sans-serif;
}
.main {
  color: #f2b138;
}

.sub {
  color: #d9d9d9;
}

.short-desc {
  color: #a1a5a6;
  font-size: clamp(10px, 4.8vw, 20px);
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: r;
  line-height: 132.4%;
  padding-top: 2.125rem;
}

.cta-btn {
  background-color: #f2b138;
  color: #353d40;
  font-family: "Raleway", sans-serif;
  font-size: 1.175rem;
  margin-top: 29px;
  width: 13.75rem;
}
/* ==================================================About */

.about-page {
  background-color: #353d40;
}

.about-title {
  padding-top: 6.5%;

  text-align: center;
}

.about-content {
  padding-top: 7%;
}

.about-desc,
h4 {
  color: #d9d9d9;
  padding-bottom: 3%;
}

.about-desc,
.about-skills {
  font-family: "Roboto", sans-serif;
}

.about-skills {
  color: #f2b138;
  line-height: 180%;
}

/* Small Mobiles =========================================================== */
@media (min-width: 320px) and (max-width: 480px) {
  .section {
    height: 60vh;
  }

  .navbar-default .navbar-brand {
    width: 11%;
    height: 20px;
    position: relative;
    margin-left: 0.4rem;
  }

  .logo {
    position: relative;
    bottom: 30px;
    width: 6.4rem;
  }

  .card {
    background-color: #353d40;
    border-radius: 0px;
    box-shadow: none;
    position: relative;
    top: 8%;
    width: 100%;
  }
  .content {
    margin-left: 0px;
  }

  .card h3 {
    font-size: 0.8em;
  }

  .heading {
    font-size: 1.855rem;
  }

  .short-desc {
    /* width: 18.378rem; */
  }

  .cta-btn {
    background-color: #f2b138;
    color: #353d40;
    font-family: "Raleway", sans-serif;
    font-size: 0.9rem;
    margin-top: 29px;
    width: 9.75rem;
  }

  /* ========= */

  .about-page {
    padding-bottom: 75vh;
  }

  .about-content p {
    text-align: center;
  }

  .about-skills {
    line-height: 155%;
    padding-top: 7%;
  }
}
